/* eslint-disable no-irregular-whitespace */
<script>
export default {
  name: "SearchIndex",
  components: {},
  data() {
    return {
      id: this.$route.query.id,
      point: this.$route.query.point,
      keyword: this.$route.query.keyword,
      lawObj: undefined
    }
  },
  created() {
    // console.log('>>>>>>>', this.$route.query)
  },
  mounted() {
    this.loadLaw()
  },
  methods: {
    loadLaw() {
      if (this.id) {
        this.$axios({
        method: 'post',
        url: '/api/law/getLawDetail',
        data: {
          id: this.id
        }
      }).then(res => {
        console.log('>>>>>>>>>>>', res)
        const resData = res.data
        const tempObj = {}
        tempObj.title = resData.title
        let tempHtml = ''
        for (const m in resData.content) {
          let point = String(m)
          if (resData.content[m].partTitle) {
            tempHtml += `<h2 id="${point}">${resData.content[m].partTitle}  ${resData.content[m].partDes}</h2>`
          }
          for (const n in resData.content[m].partContent) {
            let pPoint = point + '-' + String(n)
            if (resData.content[m].partContent[n].chapterTitle) {
              tempHtml += `<h3 id="${pPoint}">${resData.content[m].partContent[n].chapterTitle}  ${resData.content[m].partContent[n].chapterDes}</h3>`
            }
            for (const j in resData.content[m].partContent[n].chapterContent) {
              let cPoint = pPoint + '-' + String(j)
              tempHtml += `<p id="${cPoint}"><b>${resData.content[m].partContent[n].chapterContent[j].articleTitle}</b>${resData.content[m].partContent[n].chapterContent[j].articleContent}</p>`
            }
          }
        }
        tempObj.content = tempHtml
        this.lawObj = tempObj

        this.$nextTick(() => {
          document.getElementById(this.point).scrollIntoView(true)
        })
      }).catch(err => {
        console.log(err)
      })
      } else {
        alert('页面访问异常，请关闭后重试！')
      }
    },
    goBack() {
      console.log("goBack");
    }
  }
};
</script>

